@import url("https://use.typekit.net/tny8ahg.css");
@font-face {
  font-family: "Dirtyline36Daysoftype2022";
  src: local("Dirtyline 36daysoftype 2022.eot"); /* IE9 Compat Modes */
  src: url("../fonts/Dirtyline36daysoftype2022.woff") format("woff"), url("../fonts/Dirtyline36daysoftype2022.woff2") format("woff2"), url("../fonts/Dirtyline36daysoftype2022.ttf") format("truetype"); /* Safari, Android, iOS */
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-high: #fb7622;
  --color-fore: #f0f0f5;
  --color-mid: #313133;
  --color-back: #1f1f29;
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  --transition: all 400ms ease;
  --font-normal: "neue-haas-grotesk-display";
  --font-italic: "freightbigcmp-pro";
  --font-display: "Dirtyline36Daysoftype2022";
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "neue-haas-grotesk-display", sans-serif;
  font-weight: 400;
  background-color: var(--color-fore);
  color: var(--color-back);
}

/* ========================= General Styles ========================= */
.container {
  width: var(--container-width-lg);
  margin: 0, auto;
}

h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-weight: 600;
  font-size: 2.5rem;
}

a {
  color: var(--color-mid);
  transition: var(--transition);
}
a :hover {
  color: var(--color-high);
}

.btn {
  width: -moz-max-content;
  width: max-content;
  display: inline-block;
  color: var(--color-mid);
  background: none;
  padding: 0.75rem 1.2rem;
  cursor: pointer;
  border: 1px solid var(--color-mid);
  transition: var(--transition);
}
.btn:hover {
  text-decoration: none;
  border: 1px solid var(--color-mid);
  background: var(--color-mid);
  color: var(--color-fore);
}

.btn-alt {
  width: -moz-max-content;
  width: max-content;
  display: inline-block;
  color: var(--color-mid);
  padding: 0.75rem 1.2rem;
  cursor: pointer;
  background: none;
  border: 1px solid var(--color-back);
  transition: var(--transition);
}
.btn-alt:hover {
  text-decoration: none;
  border: 1px solid var(--color-back);
  background: var(--color-mid);
  color: var(--color-fore);
}

img {
  display: block;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

/* ========================= Media Queries ========================= */
@media screen and (max-width: 1400px) {
  .container {
    width: var(--container-width-md);
  }
  section {
    margin-top: 6rem;
  }
}
@media screen and (max-width: 875px) {
  .container {
    width: var(--container-width-sm);
  }
  section h2 {
    margin-top: 2rem;
  }
}/*# sourceMappingURL=index.css.map */